import back from '@/components/back'

export default {
    components:{
        back
    },
	data () { //引入PopupPicker组件 引入提现路由
		return {
			startColor: 'start1',
			DrId: this.$route.params.id, //医生id
			InYear: 2019, //年
			InMonth: 0, //月
			ServiceType: -1, //类型
			page: 1,
			size: 10,
			dataNull: true,
			show: false,//pitchOn
			expandCategory: [{ //类型渲染
				start: -1,name: '全部',class: ''},{start: 1,name: '图文问诊',class: ''},{start: 2,name: '视频问诊',class: ''},{start: 3,name: '名医续方',class: ''},{start: 21,name: '胎心判读',class: '',
			}],
			timeShow: false,
			expandTimeValue: '2019',
			format: 'YYYY',
			expandTimeTxt: '按年选择',
			expandCategoryTxtTitle: '本月',
			arrList: [],
			ConfirmIncom: 0,
			haveMore: true,
			Total: 0
		}
	},
	mounted () {
		//this.httpStart(this.$route.params.start);
		this.getMore()
	},
	methods: {
		pitchOnFn (index){
			this.expandCategory.map((v,i) => {
				if(i == index){
					v.class = 'pitchOn'
					this.ServiceType = v.start
				}else{
					v.class = ''
				}
			})
		},
		expandCalendarClickFn () {
			if(this.expandTimeTxt == '按年选择'){
				this.format = 'YYYY-MM'
				this.expandTimeTxt = '按月选择'
			}else{
				this.format = 'YYYY'
				this.expandTimeTxt = '按年选择'
			}
		},
		rlClick () {
			this.timeShow = true
		},
		pitchOnBtn () {
			this.show = false
			this.serviceList()
		},
		expandCategoryFn () {
			this.show = true
		},
		exchangeConfirm () {
			this.timeShow = false
			if(this.expandTimeValue.length > 4){
				this.InYear = this.expandTimeValue.slice(0,4)
				this.InMonth = this.expandTimeValue.slice(5)
				this.expandCategoryTxtTitle = this.expandTimeValue.replace('-','年') + '月'
			}else{
				this.InYear = this.expandTimeValue
				this.expandCategoryTxtTitle = this.expandTimeValue + '年'
			}
			this.page = 1
			this.serviceList()
		},
		exchangeEsc () {
			this.timeShow = false
		},
		serviceList (start) {
			let obj = {
				DrId: this.DrId, //医生id
				InYear: this.InYear,
				InMonth: this.InMonth,
				ServiceType: this.ServiceType,
				PageIndex: this.page,
				PageSize: this.size
			}
			this.$post('/PlatFormAPI/Doctor/QueryDoctorInDetailByPage',obj).then(o => {
				if(o.ReturnData != null && o.ReturnData != undefined && o.ReturnData != []){
					this.ConfirmIncom = o.ConfirmIncom;
					this.Total = o.Total;
				}else{
					if(start){
						return this.dataNull = true
					}else{
						this.dataNull = false
					}
				}
				if(o.ReturnData != null){
					this.dataNull = true
				}
				if (start){
					o.ReturnData.forEach(v => {
						this.arrList.push(v)
					})
				}
				else {
					this.arrList = o.ReturnData
				}
			})
		},
		typeFn (n) {
			switch(n){
				case 1:
					return '图文问诊';
				case 2:
					return '视频问诊';
				case 3:
					return '名医续方';
				case 4:
					return '处方费';
				case 5:
					return '护理咨询';
				case 6:
					return '远程护理';
				case 7:
					return '上门服务';
				case 8:
					return '耗材';
				case 9:
					return '服务包';
				case 21:
					return '母胎解读';
				case 99:
					return '其他';											
			}
		},
		accomplishFn (n) {
			switch(n){
				case 0:
					return '无';
				case 1:
					return '进行中';
				case 2:
					return '已完成';
				case 3:
					return '已退款';										
			}
		},
		NumTypeFn (n){
			switch(n){
				case 1:
					return '次';
				case 2:
					return '期';
				case 3:
					return '台';
				case 4:
					return '人';
				case 5:
					return '疗程';									
			}
		},
		interceptionFn (n){
			return n.slice(5)
		},
		getMore(){ //下拉加载分页
			const el = this.$refs.el
			let timer = null
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop
					|| window.pageYOffset
					|| document.body.scrollTop,
					h = el.scrollHeight,
					wh = window.innerHeight
					if (h - top - wh < 100 && this.haveMore){
						if(this.Total > this.size){
							this.page += 1
							this.serviceList(true)
						}
					}
				}, 100)
			}
		}
	},
	beforeDestroy(){
		window.onscroll = null
	},
	created(){
		this.serviceList();
	}
}



